<template>
  <div>
    <div class="management-frame" v-if="managedNickname">
      <div class="management-plate">
        <div class="management-plate-background"></div>
        Управление аккаунтом<br>{{ managedNickname }}
        <img src="@/assets/newImages/black_cross_icon.png" alt="" class="stop-managing-icon" @click="stopManaging()">
      </div>
    </div>
    <Loader :visible="loading" />
    <div class="app-main-layout" :class="loading ? 'hidden' : 'visible'">
    <Navbar class="navbar" @click="isOpen=!isOpen" />
    <Sidebar class="sidebar" v-model="isOpen" :value="isOpen" @closeSidebar="closeSidebar" @forceUpdateAll="forceUpdateAll"/>
    <div class="darken" v-if="isOpen" @click="isOpen=!isOpen"></div>
      <main class="app-content" :style="{ background: backgroundImage ? `url(${backgroundImage}) center / cover` : background }">
      <!-- <main class="app-content"> -->
        <div class="app-page cabinet">
          <PageNavbar />
          <DreamCounters class="mobile-counters"/>
          <router-view @mountedChild="mountedChild" @startManaging="startManaging" @stopManaging="stopManaging" />
          <transition name="pop" mode="out-in">
            <MascotPopupWindow :title="firstMascotModalTitle" :text="firstMascotModalText" v-if="showFirstMascotModal" @closeModal="closeFirstMascotModal"/>
          </transition>
          <transition name="pop" mode="out-in">
            <MascotPopupWindow  :type="'gift'" :title="secondMascotModalTitle" :text="secondMascotModalText" v-if="showSecondMascotModal" @closeModal="closeSecondMascotModal"/>
          </transition>
        </div>
      </main>
    </div>
  </div>
   <!-- :style="'background-image: red!important'" -->
</template>


<script>
import Navbar from '@/components/app/Navbar.vue'
import Sidebar from "@/components/app/Sidebar.vue";
import PageNavbar from "@/components/app/PageNavbar.vue";
import { defineComponent } from "@vue/runtime-core";
import Loader from '@/components/app/Loader.vue';
import { mapState } from "vuex";
import DreamCounters from '../components/DreamCounters.vue';
import MascotPopupWindow from '@/components/app/MascotPopupWindow.vue';
import localizeFilter from "@/filters/localize.filter";

export default defineComponent({
  name: 'main-layout',
  data: () => ({
    isOpen: false,
    showFirstMascotModal: false,
    showSecondMascotModal: false,
    firstMascotModalTitle: localizeFilter('mascot_title_1'),
    secondMascotModalTitle: localizeFilter('mascot_title_2'),
    backgroundImage: '',
    background: '',
    walletBackgrounds: [
      require('@/assets/newImages/backgrounds/wallet/1.png'),
      require('@/assets/newImages/backgrounds/wallet/2.png'),
      require('@/assets/newImages/backgrounds/wallet/3.png'),
    ],
    legalAdviceBackgrounds: [
      '#B4EAD3'
    ],
    otherBackgrounds: [
      // TODO: add mobile versions of the backgrounds
      require('@/assets/newImages/backgrounds/other/1.png'),
      require('@/assets/newImages/backgrounds/other/2.png'),
      require('@/assets/newImages/backgrounds/other/3.png'),
      require('@/assets/newImages/backgrounds/other/4.png'),
      // require('@/assets/newImages/backgrounds/other/5.png'),
      // require('@/assets/newImages/backgrounds/other/6.png'),
      // require('@/assets/newImages/backgrounds/other/7.png'),
      // require('@/assets/newImages/backgrounds/other/8.png'),
      // require('@/assets/newImages/backgrounds/other/9.png'),
    ],
    managedNickname: '',
  }),
  components: {
    Sidebar,
    Navbar,
    Loader,
    PageNavbar,
    DreamCounters,
    MascotPopupWindow
},
  computed: {
    ...mapState("loader", ["loading"]),
    name() {
      return (this.$store.getters.profile ? (this.$store.getters.profile.firstName + ' ' + this.$store.getters.profile.lastName) : '')
    },
    firstMascotModalText(){
      return (localizeFilter('mascot_text_1') + this.name + '!')
    },
    secondMascotModalText(){
      return (localizeFilter('mascot_text_2') + this.name + '!')
    }
  },
  methods: {
    closeSidebar() {
      this.isOpen=false
    },
    closeFirstMascotModal() {
      this.showFirstMascotModal=false
      setTimeout(()=>{this.showSecondMascotModal=true},1000)

    },
    closeSecondMascotModal() {
      this.showSecondMascotModal=false

    },
    forceUpdateAll() {
      // this.$forceUpdate()
      // this.$router.go()
      // window.location.reload()
    },
    mountedChild(pageName) {
      // console.log(pageName)
      let backgrounds=this.otherBackgrounds;
      if (pageName == 'Wallet') {
        backgrounds=this.walletBackgrounds;
      } else if (pageName == 'LegalAdvice') {
        backgrounds=this.legalAdviceBackgrounds;
      }
      let background=backgrounds[Math.floor(Math.random() * backgrounds.length)];
      if (background.startsWith('#')) {
        this.background=background;
        this.backgroundImage='';
      } else {
        this.backgroundImage=background;
        this.background='';
      }
      // this.backgroundImage=backgrounds[Math.floor(Math.random() * backgrounds.length)]
    },
    startManaging(managedId, managedNickname) {
      console.log(managedId, managedNickname)
      localStorage.managedId=managedId
      localStorage.managedNickname=managedNickname
      this.managedNickname=managedNickname
      this.$router.push({ name: 'cabinetHome' })
      this.$store.dispatch('getProfile')
      this.$store.dispatch('getBalance')
    },
    stopManaging(){
      localStorage.removeItem('managedId')
      localStorage.removeItem('managedNickname')
      this.managedNickname=''
      this.$router.push({ name: 'StructureControl' })
      this.$store.dispatch('getProfile')
      this.$store.dispatch('getBalance')
    }
  },
  mounted() {
    this.managedNickname=localStorage.managedNickname
    if(localStorage.getItem("mascotTimestamp")===null|| (Date.now() - localStorage.mascotTimestamp >=(24*60*60000))){
      setTimeout(()=>{this.showFirstMascotModal=true},1000)
      console.log(24*60*60000)
      localStorage.mascotTimestamp=Date.now()
    }    
  },
});
</script>


