<template>
<div class="pager-item" :style="itemStyle">
    <div class="text-block">
        <!-- <div class="title" :style="isMobile ? (content ?? {}).titleStyleMobile : (content ?? {}).titleStyle">{{ (content ?? {}).title }}</div> -->
        <div class="title">
            {{ (content ?? {}).title }}
        </div>
        <!-- <p :style="isMobile ? (content ?? {}).textStyleMobile : (content ?? {}).textStyle">{{ (content ?? {}).text }}</p> -->
    </div>
    <div class="image-block" :style="(content ?? {}).imageBlockStyle">
        <img class="pager-item-image" :src="(content ?? {}).imageLink" v-if="(content ?? {}).imageLink" :style="(content ?? {}).imageStyle">
    </div>
    
    <button class="blue-button" :style="(content ?? {}).buttonStyle" v-if="(content ?? {}).buttonTitle != null && (content ?? {}).buttonLink != null" @click="openLink((content ?? {}).buttonLink, (content ?? {}).isRouterLink == true)">
                {{ $filters.capitalizeFirstLetter((content ?? {}).buttonTitle) }}
    </button>
</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: {
        content: Object,
        itemStyle: {
            type: String,
            default: null
        }
    },
    setup() {
        
    },
    mounted() {
        // console.log('item mounted')
    },
    computed: {
    isMobile() {
        return this.$store.getters.isMobile
    }
  },
    methods: {
        openLink(link,isRouterLink) {
            // console.log(isRouterLink)
            if (isRouterLink) {
                this.$router.push(link)
            } else {
                // console.log(link)
                window.open(link)
            }
        }
    },
})
</script>

