const router = createRouter({
  mode: 'history',
  history: createWebHashHistory(),
  routes
})

router.beforeEach((to, from, next) => {
  const session = localStorage.dreamOfLifeClientSession
  const timestamp = Number(localStorage.dreamOfLifeClientSessionExpireTimestamp) || null
  let sessionIsExpired = false
  if (timestamp) {
    sessionIsExpired = Date.now() > timestamp
  }

  // TODO: add partner session expire timestamp in router and partner api
  const partnerSession = localStorage.partnerSession

  to.hash = ''
  document.title = to.meta.title ? to.meta.title : "Dream of Life"

  if (to.meta.requiresAuth) {
    if (session == null || session == undefined || sessionIsExpired) {
      next({ name: 'login' })
    } else {
      next()
    } 
  } else if (to.meta.requiresAuthPartner) {
    if (partnerSession == null || partnerSession == undefined) {
      next({ name: 'login' })
    } else {
      next()
    }
  } else
    if (to.name == 'login') {
      if (session != null && session != undefined && !sessionIsExpired) {
        next({ name: 'cabinetHome' })
      } else if (partnerSession != null && partnerSession != undefined) {
        next({ name: 'cabinetParnterHome' })
      } else {
        next()
      }
    } else {
      next()
    }
})

export default router
