import { api } from '@/plugins/api'
import localizeFilter from '@/filters/localize.filter'

export default {
    getters: {
    },
    actions: {
      async register ({ dispatch, commit }, {email, firstName, nickname, lastName, telegramName, sponsorEmail, password, age, phoneNumber, country, sex}) {
          try {
            await api.register( {email, firstName, nickname, lastName, telegramName, sponsorEmail, password, age, phoneNumber, country, sex})
            commit('SET_LOGGINED', true)
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },

        async login ({ dispatch, commit }, {email, password}, ctx) {
          try {
            await api.login({email, password})
            commit('SET_LOGGINED', true)
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },

        async requestRestorePassword ({ dispatch, commit }, {email}) {
          try {
            await api.requestRestorePassword({email})
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },

        async restorePassword ({ commit }, {newPassword, code}) {
          try {
            await api.restorePassword({newPassword, code})
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },

        async activateEmail ({ commit }, {token}) {
          try {
            let {token: sessionId} = await api.activateEmail({token})
            localStorage.dreamOfLifeClientSession = sessionId;
            api.setCookie(sessionId);
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },

        async getEmailByReferralToken ({ commit },{token}) {
          try {
            return await api.getEmailByReferralToken({token})
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },

        async logout ({ dispatch, commit }) {
          try {
            await api.logout({})
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },
    }
}