               <p class="text">{{text}}</p>
               <button class="blue-button" v-if="buttonTitle != null && buttonAction != null" @click="buttonAction" :style="{ background: backgroundButton}">
                       {{ buttonTitle }}
               </button>
           </div>
           <div class="media-block" :class="(alignContentFromLeftToRight ? 'left':'right') + ' ' + (mediaClass ?? '')">
               <img class="image" v-bind:src="imageLink" :style="{ height: (isMobile ? heightMobile : height)}">
           </div>
           </div>
       </div>
   </section>
</template>
   
   <script>
   import { defineComponent } from '@vue/composition-api'
   import localizeFilter from "@/filters/localize.filter";
   
   export default defineComponent({
       props: {
           alignContentFromLeftToRight: {
               type: Boolean,
               required: true
           },
           title: String,
           text: String,
           imageLink: String,
           mediaClass: String,
           buttonTitle: {
               type: String,
               default: null
           },
           buttonAction: {
               type: Function,
               default: null
           },
           backgroundColor: String,
           backgroundButton: String
       },
       computed: {
           isMobile() {
               return this.$store.getters.isMobile
           }
     },
     methods: {
     },
   })
   </script>
   
