import { api } from '@/plugins/api'
import localizeFilter from '@/filters/localize.filter'

let mappers = {
  operation: (op)=>{
    if(op.comment){
      if(op.comment.operationName == 'transferTo'){
        op.name = localizeFilter('operation_transfer_to') + op.comment.nickname;
      } else if(op.comment.operationName == 'transferFrom'){
        op.name = localizeFilter('operation_transfer_from') + op.comment.nickname;
      } else if(op.comment.operationName == 'joinHouse'){
        op.name = localizeFilter('operation_join_house');
      } else if(op.comment.operationName == 'matrixBounty'){
        op.name = localizeFilter('operation_matrix_bounty');
      } else if(op.comment.operationName == 'matrixReferralBounty'){
        op.name = localizeFilter('operation_matrix_referral_bounty');
      } else if(op.comment.operationName == 'instantLoan'){
        op.name = localizeFilter('operation_instant_loan');
      } else if(op.comment.operationName == 'instantLoanReturn'){
        op.name = localizeFilter('operation_instant_loan_return');
      } else if(op.comment.operationName == 'fillSigma'){
        op.name = localizeFilter('operation_sigma_balance_fill'); // Пополнение баланса Sigma
      } else if(op.comment.operationName == 'fillDeposit'){
        op.name = localizeFilter('operation_opening_contract_number') + (op.comment.fillContractId || op.comment.fillDepositId); // Открытие договора №
      } else if(op.comment.operationName == 'sigmaInterestRate'){
        op.name = localizeFilter('operation_sigma_interest_rate'); // Дивиденды от Sigma
        op.percent = op.comment.percent;
      } else if(op.comment.operationName == 'depositInterestRate'){
        op.name = localizeFilter('operation_deposit_interest_rate_contract_number') + (op.comment.fillContractId || op.comment.fillDepositId); // Выплата по договору №
      } else if(op.comment.operationName == 'marketingBonus'){
        op.name = localizeFilter('operation_marketing_bonus'); // Дивиденды по маркетингу
        op.percent = op.comment.percent;
      }
    } else {
      if(op.type == 'fill' || op.type == 'fill_balance'){
        op.name = localizeFilter('operation_balance_fill'); // Пополнение баланса
      } else if(op.type == 'withdraw' || op.type == 'withdraw_balance'){
        op.name = localizeFilter('operation_balance_withdraw'); // Вывод с баланса
      }
    }
    return op;
  },
  contractObject: (co)=>{
    let getContractObjectByParams = {
      auto: ({mark, model, state, year})=> `${localizeFilter(state)} ${mark} ${model}, ${year}`,
      target: ({product})=>`${product}`,
      realEstate: ({country, city, area, propertyType})=>`${localizeFilter(propertyType)}, ${area} ${localizeFilter('meters_2')}, ${city}, ${country}`,
      closeCredit: ({creditObject})=>`${creditObject}`
    }
    if(co.params){
      co.object = getContractObjectByParams[co.type](co.params);
    }
    return co;
  }
};


