import store from '../store'
import ru from '../locales/ru.json'
import en from '../locales/en.json'
import es from '../locales/es.json'
import bg from '../locales/bg.json'
import tr from '../locales/tr.json'
import pt from '../locales/pt.json'
import kz from '../locales/kz.json'

const locales = {
  'ru-RU': ru,
  'en-US': en,
  'es-ES': es,
  'bg-BG': bg,
  'tr-TR': tr,
  'pt-PT': pt,
  'kz-KZ': kz,
}

export default function localizeFilter(key, args = []) {
  const locale = store.state.locale
  let result = locales[locale][key] || key //|| `[Localize error]: key ${key} not found`
  result = sprintf(result, ...args);
  return result
}

function sprintf(str) {
  var args = arguments, i = 1;

  return str.replace(/%(s|d|0\d+d)/g, function (x, type) {
      var value = args[i++];
      switch (type) {
      case 's': return value;
      case 'd': return parseInt(value, 10);
      default:
          value = String(parseInt(value, 10));
          var n = Number(type.slice(1, -1));
          return '0'.repeat(n).slice(value.length) + value;
      }
  });
}