<template>
    <!-- :autoplay="2000" :wrap-around="true" -->
    <!-- Carousel element is section tag with class "carousel" -->
    <Carousel :autoplay="5000" :wrap-around="true" :transition="500" :touchDrag="false" :mouseDrag="false"> 
    <Slide v-for="slide in slides" :key="slide.id">
      <div class="carousel__item" :style="{ backgroundImage: 'url(' + slide.image + ')', backgroundPosition: position(slide).background ? position(slide).background : undefined}">
          <div v-html="slide.title" class="title" :style="{ left: position(slide).left ? position(slide).left : undefined, top: position(slide).top ? position(slide).top : undefined, right: position(slide).right ? position(slide).right : undefined, bottom: position(slide).bottom ? position(slide).bottom : undefined }" ></div>
      </div>
    </Slide>

  </Carousel>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import { Carousel, Slide } from 'vue3-carousel';

import 'vue3-carousel/dist/carousel.css';

export default defineComponent({
    components: { Carousel, Slide },
    props: {
        titles: Array
    },
    data() {
        return {
            slides: [
                {
                    id: 1,
                    image: require('@/assets/newImages/slide_1.png'),
                    title: this.$localizeFilter('landing_carousel_title_1'),
                    position: {
                        left: '79px',
                        bottom: '45px'
                    },
                    positionMobile: {
                        top: '-100px'
                    }
                },
                {
                    id: 2,
                    image: require('@/assets/newImages/slide_2.png'),
                    title: this.$localizeFilter('landing_carousel_title_2'),
                    position: {
                        left: '79px',
                        bottom: '45px'
                    },
                    positionMobile: {
                        top: '-100px'
                    }
                },
                {
                    id: 3,
                    image: require('@/assets/newImages/slide_3.png'),
                    title: this.$localizeFilter('landing_carousel_title_3'),
                    position: {
                        left: '79px',
                        bottom: '45px'
                    },
                    positionMobile: {
                        top: '-100px'
                    }
                },
                {
                    id: 4,
                    image: require('@/assets/newImages/slide_4.png'),
                    title: this.$localizeFilter('landing_carousel_title_4'),
                    position: {
                        left: '79px',
                        bottom: '45px'
                    },
                    positionMobile: {
                        top: '-100px'
                    }
                },
            ]
        }
    },
    setup() {
        
    },
    methods: {
        position(slide) {
            return this.isMobile ? slide.positionMobile : slide.position;
        }
    },
    computed: {
        isMobile() {
            return this.$store.getters.isMobile
        }
  },
})
</script>

