import { createApp } from "vue";
import App from "./App.vue";
import VueGtag from "vue-gtag";
import 'materialize-css'
import 'materialize-css/dist/js/materialize.js'
import 'materialize-css/dist/js/materialize.min.js'
import 'materialize-css/dist/css/materialize.css'
import "materialize-css/dist/css/materialize.min.css";
import "material-design-icons/iconfont/material-icons.css";
import Pagination from 'v-pagination-3';
import router from './router'
import store from './store'
import filters from './filters/filters.js'
import localizeFilter from './filters/localize.filter'
import dateFilter from './filters/date.filter'
import currencyFilter from './filters/currency.filter'
import messagePlugin from '@/plugins/message.plugin'
import { createVuestic } from 'vuestic-ui'
import Vue3TouchEvents from "vue3-touch-events";
// import api from '@/plugins/api'


const app = createApp(App).use(store).use(router).use(messagePlugin)
app.use(createVuestic())
app.use(Vue3TouchEvents);
// app.use(api)
app.component('pagination', Pagination);
app.config.globalProperties.$filters = filters
app.config.globalProperties.$localizeFilter = localizeFilter
app.config.globalProperties.$dateFilter = dateFilter
app.config.globalProperties.$currencyFilter = currencyFilter
store.$app = app;
app.use(VueGtag, {
    config: { id: "G-L44LXV8701" }
  }).mount('#app')