
import { createStore } from 'vuex'
import auth from './auth' 
import info from './info'
import { loader } from './loader';
import localizeFilter from '@/filters/localize.filter'
import countries from '@/assets/countries.json'

// Create a new store instance.
export default createStore({
    state: () => ({
      isLoggined: null,
      error: null,
      locale: localStorage.depositClientLocale ?? "en-US",
      currency: "USD"
    }),
    getters: {
        error: s => s.error,
        isLoggined: state => {
            return state.isLoggined
        },
        locales() {
            // return ['en-US','es-ES','ru-RU']
            return ['en-US', 'tr-TR', 'es-ES', 'pt-PT', 'ru-RU', 'kz-KZ', 'bg-BG'] 
        },
        countries() {
            return countries
        },
        localizedCountries(state){
            return countries.map(country => {
                return {
                    text: country.name[state.locale.split('-')[0]] || country.name['en'],
                    value: country.alpha2
                }
            })
        },
        countryByCode(state){
            return Object.fromEntries(countries.map(country => [country.alpha2, country.name[state.locale.split('-')[0]] || country.name['en']]))
        },
        isMobile() {
            if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
                return true
            } else {
                return false
            }
        }
    },
    modules: {
        auth, info, loader
    },
    mutations: {
        SET_LOGGINED(state, data) {
            state.isLoggined = data
        },
        SET_ERROR(state, error) {
            state.error = error
        },
        SET_LOCALE(state, locale) {
            state.locale = locale
            localStorage.depositClientLocale = locale
        },
        CLEAR_ERROR(state) {
            state.error = null
        }
    },
    actions: {
        // fetch locale/currency/etc
    }
  })
