<template>
    <div id="pager" class="pager">
    <div v-for="(item, idx) in pagerContent" :key="idx" class="page" :style="pageStyle">
      <PagerItem 
      v-bind:content="(pagerContent ?? [])[idx]"
      v-bind:itemStyle="itemStyle"
      />
    </div>
</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import PagerItem from './PagerItem'

export default defineComponent({
  props: [
    'pagerContent', 'pageStyle', 'itemStyle'
  ],
    components: {
      PagerItem
    },
    setup() {
        
    },
    data: () => ({
        current: 0,
        direction: 1,
        transitionName: "fade",
        show: false,
        // pages: [
        // { className: "blue" },
        // { className: "red" },
        // { className: "yellow" }
        // ]
  }),
  methods: {
    page(dir) {
      // console.log('turned')
      this.direction = dir;
      dir === 1
        ? (this.transitionName = "page-next")
        : (this.transitionName = "page-prev");
      var len = this.pagerContent.length;
      this.current = (this.current + dir % len + len) % len;
    }
  },
  mounted() {
    this.show = true;
  },
  computed: {
  },
})
</script>

