const hostName = location.origin
// const hostName = 'https://ee70-91-90-11-32.eu.ngrok.io'

export default {
    getHostName: function() {
        return hostName
    },
    getWSHostName: function() {
        return hostName.replace('https','wss').replace('http','ws')
    },
}