<template>
    <div class="avatar-container" :class="avatarType">
      <label v-if="uploadable && !avatarLink" class="upload">
        Загрузить фото <input type="file" id="input" ref="inputFile" @change="uploadFile" >
      </label>
      <div v-if="avatarLink" class="profile-picture">
        <img :src="avatarLink" alt="">
      </div>
      <label v-if="avatarLink && editable" class="edit">
        <img src="@/assets/newImages/pencil_icon.png" alt="" class="pencil">
        <input type="file" id="input" ref="inputFile" @change="uploadFile">
      </label>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from "@/filters/localize.filter";
import {api} from "@/plugins/api";

export default defineComponent({
  props: {
      uploadable: Boolean,
      editable: Boolean,
      avatarType: String,
      avatarLink: String
  },
  components: {
      
  },
  data: () => ({
      
  }),
  methods: {
    async uploadFile(event){
      console.log(event.target.files[0]);
      
      try {
        let data = await api.uploadAvatar(event.target.files[0])
        this.$store.dispatch('setAvatarLink', data.data)
        this.$message(localizeFilter('profile_photo_updated'))
      } catch(e) {
        console.log(e);
      } finally {
        this.$refs.inputFile.value = null;
      }
      // let file = 
    }
  },
  computed: {
    
  },
  mounted() { 
    console.log(this.avatarLink)
    // window.addEventListener('scroll', this.handleScroll);
  },
});
</script>

