<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: {
        title: String,
        text: String,
        videoLink: String,
        background: String
    },
    setup() {
        
    },
    methods: {
        register() {
            this.$router.push('/register')
        },
    },
    computed: {
        frameWidth() {
            return Math.min(Math.min(window.innerWidth, window.screen.availWidth) - 40, 1200);
        },
        frameHeight() {
            return this.frameWidth * 0.5625;
        }
  },
})
