<template>
    <div class="dropdown">
        <div v-for="locale,idx in locales" v-bind:key="idx" class="dropdown-item">
            <img :src="require('@/assets/newImages/flags/flag_' + locale +'.png')" class="flag">
            <button class="lang-button" @click="changeLocale(locale)">{{$localizeFilter('locale_'+locale)}}</button>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import {getCurrentInstance} from 'vue'

export default defineComponent({
    setup() {
        
    },
    computed: {
        locales() {
            return this.$store.getters.locales
        },
    },
    methods: {
        changeLocale(locale) {
            this.$store.commit('SET_LOCALE', locale)
            this.$emit('closeDropdown')
            this.$router.go()
        }
    }
})
</script>

