<template>
    <div class="question" @click="(e) => handleClick(e)" :style="{width: this.isOpen? maxWidth:width}">
        <div class="title-block">
            <img class="mark" src="@/assets/newImages/plus.png">
             <p class="title"> {{content.title ?? ''}}</p>
        </div>
        <p class="text" v-if="isOpen">
            {{content.text ?? ''}}
        </p>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: [
        'content',
        'width',
        'maxWidth'
    ],
    data: () => ({
        isOpen: false
    }),
    setup(props) {
        const currentWidth=props.width
        return currentWidth
        
    },
    methods: {
        handleClick(e) {
            // console.log(e)
            e.target.blur()
            this.isOpen = !this.isOpen
        }
    },
})
</script>

