<template>
<div class="sidebar" :class="{open: value}">
  <div class="top-block">
    <router-link :to="'/'">
    <div class="sidebar-logo">
          <img class="logo-image" src="@/assets/newImages/logo_plate.png">
          <!-- <p class="logo-text">{{$localizeFilter('landing_main_section_title')}}</p> -->
    </div>
    </router-link>
    <button class="cross" @click="closeSidebar"></button>
  </div>
  <ul class="sidebar-list">
    <li v-for="(link,index) in links" class="sidebar-list-item" v-bind:key="index">
        <router-link 
        class="sidebar-button"
        active-class="active"
        tag="button"
        :to="link.url"
        :exact="link.exact"
        @click="scrollFix(link.url)"
        >{{ link.title }}</router-link>
    </li>
    <li>
      <router-link
        class="sidebar-button"
        active-class="active"
        tag="button"
        :to="'/drmc-landing'"
        @click="scrollFix(link.url)">
        <div class="flex-button"><img class="drmc-image" src="@/assets/newImages/dream_coin_icon.png">{{$localizeFilter('DRMC')}}</div>
        </router-link>
    </li>
  </ul>
  <div class="dropdown-block">
      <LangsDropdownTrigger @triggerDropdown="triggerDropdown"/>
      <div class="place-for-dropdown">
        <LangsDropdown class="dropdown" v-if="showDropdown" @closeDropdown="closeDropdown"/>
      </div>
  </div>

</div>
</template>

<script>
import localizeFilter from "@/filters/localize.filter";
import { defineComponent } from "@vue/runtime-core";
import LangsDropdownTrigger from "@/components/app/langs/LangsDropdownTrigger.vue"
import LangsDropdown from "@/components/app/langs/LangsDropdown.vue"

export default defineComponent({
  setup() {
    // console.log(this.data.links);
  },
  components: {
    LangsDropdownTrigger, LangsDropdown
  },
  props: ["value"],
  data: function() {
    return {
      links: [
        { title: localizeFilter("home"), url: "/#home", exact: true },
        // { title: "Планы", url: "/plans" },
        // { title: "Легальность", url: "#" },
        { title: localizeFilter('outer_page_realEstate'), url: "/real-property-landing" },
        // { title: localizeFilter('real_estate_not_redundant'), url: "/#real_estate_not_redundant" },
        { title: localizeFilter('landing_hotels'), url: "/hotels-landing" },
        { title: localizeFilter('landing_startups'), url: "/startups-landing" },
        // { title: localizeFilter('why_turkey'), url: "/#why_turkey" },
        // { title: localizeFilter('get_citizenship'), url: "/#get_citizenship" },
        { title: localizeFilter('registration'), url: "/register" },
        { title: localizeFilter('login'), url: "/login" },
      ],
      showDropdown: false
    };
  },
  watch: {
    $route(to, from) {
      this.$emit('closeSidebar')
    }
  },
  methods: {
    scrollFix: function(hashbang) {
        if (hashbang.indexOf('#') === -1) {
          window.scrollTo({top: 0, behavior: 'auto'})
          return
        }
        const element = document.getElementById(hashbang.replace('#',''))
        if (element) {
          const y = element.getBoundingClientRect().top + window.pageYOffset;
          window.scrollTo({top: y, behavior: 'auto'});
        }
    },
    triggerDropdown() {
      this.showDropdown = !this.showDropdown
    },
    closeDropdown() {
      this.showDropdown = false
    },
    closeSidebar() {
      this.$emit('closeSidebar')
    }
  }
});
</script>

