<template>
    <div id="dropdown-trigger" class="dropdown-trigger">
        <button class="transparent-button" @click="triggerDropdown">
            <img :src="require('@/assets/newImages/flags/flag_' + locale +'.png')" class="flag">
            <img src="@/assets/newImages/dark_blue_arrow.png" class="arrow" :class="isOpen ? 'top' : 'bottom'">
        </button>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    setup() {
        
    },
    data: () => ({
        isOpen: false,
    }),
    computed: {
        locale() {
            return this.$store.state.locale
        }
    },
    methods: {
        triggerDropdown() {
            this.$emit('triggerDropdown')
        }
    }
})
</script>

