const testSession = null//"uojjfcKUch6fVuPE5gvq7NmoSu4iWe4R";

function setCookie (token) {
    const domain = globalConfig.getHostName();
    const domainEx = [ '', ...domain.split('.').slice(1) ].join('.');
    const date = new Date(Date.now() + 365 * 24 * 60 * 60 * 1000);
    document.cookie = `sessionId=${token}; expires=${date.toGMTString()}; path=/; domain=${domainEx}`;
}

async function remoteCall (method, input) {
    try {
        if (typeof input.cookies !== "object") input.cookies = {};
        if (input.cookies === null) input.cookies = {};
        let savedSession = localStorage.dreamOfLifeClientSession
        let managedId = localStorage.managedId
        if(managedId && prohibitedManagementMethods.includes(method)){
            throw new Error("Non-allowed method for managed client")
        }
        input.cookies.sessionId = input.cookies.sessionId || testSession || savedSession || null;
        input.managedId = managedId || null;
        const { err, data, sessionExpireTimestamp } = await httpClient.post(`${globalConfig.getHostName()}/call/client/${method}`, input).catch((err)=>err.response.data);
        if (err && (err.message === "Invalid token" || err.message === "Access denied")) {
            delete localStorage.dreamOfLifeClientSession;
            delete localStorage.managedId;
            delete localStorage.dreamOfLifeClientSessionExpireTimestamp;
        }
        if (err) throw err;
        if (data && data != undefined) { if (data.err && data.err != null) throw Error(data.err.message); }
        if (method === "login" || method === "register") {
            // console.log(data)
            localStorage.dreamOfLifeClientSession = data.token;
            setCookie(data.token);
        }
        if (method === "logout") {
            delete localStorage.dreamOfLifeClientSession;
        }
        if (localStorage.dreamOfLifeClientSession != null && localStorage.dreamOfLifeClientSession != undefined) {
            localStorage.dreamOfLifeClientSessionExpireTimestamp = sessionExpireTimestamp || null
        }
        return data;
    } catch (err) {
        throw err;
    }
}

const api = {};
api.setCookie = setCookie;
for (let i of methodNames) {
    api[i] = async (data) => {
        return remoteCall(i, data);
    }
}

api.uploadAvatar = async (file) => {
    let formData = new FormData();
    formData.append('avatar', file);
    formData.append('sessionId', localStorage.dreamOfLifeClientSession);
    try {
        let {data} = await axios.post( `${globalConfig.getHostName()}/upload-avatar`,
            formData,
            {
                headers: {
                    'Content-Type': 'multipart/form-data',
                    // 'Cookie': `sessionId=${localStorage.dreamOfLifeClientSession}`
                },
                // withCredentials: true
            }
        )
        return data;
    } catch(e) {
        throw e
    }
}

