<template>
    <div class="buy-coin-popup-window" :id="windowId" :class="`${view} ${position}`" :style="windowStyle">
        <button class="mascot-back-button" @click="closeModal"></button>
        <div class="title">{{title}}</div>
        <div class="subtitle">{{text}}</div>

        <img class="mascot gift" src="@/assets/newImages/mascot_with_gift.png" v-if="type=='gift'">
        <img class="mascot" src="@/assets/newImages/mascot.png" v-else>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: {
        buyCoinLink: {
            type: String,
            default: null
        },
        isRouterLink: {
            type: Boolean,
            default: false
        },
        view: {
            type: String,
            default: 'coin' // coin, bigCoinLight, bigCoinDark
        },
        position: {
            type: String,
            default: 'bottomLeft' // bottomLeft, center
        },
        title: {
            type: String,
            required: true
        },
        text: {
            type: String,
            required: true
        },
        type: {
            type: String,
            required: false
        },

    },
    data: ()=> ({
        mountDate: +new Date(),
        windowStyle: ''
    }),
    computed: {
        windowId() {
            return `buyCoinPopupWindow-${this.mountDate}`
        }
    },
    setup() {
        
    },
    mounted() {
        this.$nextTick(()=>{
            const el = document.getElementById(this.windowId)
            if (el) {
                 const windowWidth = el.offsetWidth
                 const windowHeight = el.offsetHeight
                 this.windowStyle = ((this.position === 'center') ? `margin-left: -${windowWidth/2.0}px; margin-top: -${windowHeight/2.0}px;` : '')
            }
           
        })
    },
    methods: {
    closeModal() {
        this.$emit('closeModal')
    }
  }
})
</script>

